import * as d3 from 'd3';

interface Datum {
  x: number;
  y: number;
  type: string; // Optional type for categorization
}

@Component({
  tag: 'scatter-plot',
  styleUrl: 'scatter-plot.css',
  shadow: true,
})
export class ScatterPlot {
    @Prop() data: Datum[] =[
             // Type A: y = 2x + 5 + noise
            {x: 0, y: 4.2, type: "A"}, {x: 3, y: 11.5, type: "A"}, {x: 6, y: 17.8, type: "A"},
            {x: 9, y: 23.9, type: "A"}, {x: 12, y: 29.2, type: "A"}, {x: 15, y: 35.1, type: "A"},
            {x: 18, y: 41.9, type: "A"}, {x: 21, y: 47.8, type: "A"}, {x: 24, y: 54.7, type: "A"},
            {x: 27, y: 60.8, type: "A"}, {x: 30, y: 67.7, type: "A"}, {x: 33, y: 73.8, type: "A"},
            {x: 36, y: 80.7, type: "A"}, {x: 39, y: 87.6, type: "A"}, {x: 42, y: 94.5, type: "A"},
            {x: 45, y: 101.4, type: "A"}, {x: 48, y: 108.3, type: "A"}, {x: 51, y: 115.2, type: "A"},
            {x: 54, y: 122.1, type: "A"}, {x: 57, y: 129.0, type: "A"}, {x: 60, y: 135.9, type: "A"},
            {x: 63, y: 142.8, type: "A"}, {x: 66, y: 149.7, type: "A"}, {x: 69, y: 156.6, type: "A"},
            {x: 72, y: 163.5, type: "A"}, {x: 75, y: 170.4, type: "A"}, {x: 78, y: 177.3, type: "A"},
            {x: 81, y: 184.2, type: "A"}, {x: 84, y: 191.1, type: "A"}, {x: 87, y: 198.0, type: "A"},
            {x: 90, y: 204.9, type: "A"}, {x: 93, y: 211.8, type: "A"}, {x: 96, y: 218.7, type: "A"},
            // Type B: y = 1.5x + 10 + noise
            {x: 1, y: 11.3, type: "B"}, {x: 4, y: 16.7, type: "B"}, {x: 7, y: 21.4, type: "B"},
            {x: 10, y: 25.3, type: "B"}, {x: 13, y: 29.5, type: "B"}, {x: 16, y: 34.4, type: "B"},
            {x: 19, y: 38.7, type: "B"}, {x: 22, y: 43.1, type: "B"}, {x: 25, y: 47.5, type: "B"},
            {x: 28, y: 52.1, type: "B"}, {x: 31, y: 56.5, type: "B"}, {x: 34, y: 61.1, type: "B"},
            {x: 37, y: 65.7, type: "B"}, {x: 40, y: 70.3, type: "B"}, {x: 43, y: 74.9, type: "B"},
            {x: 46, y: 79.5, type: "B"}, {x: 49, y: 84.1, type: "B"}, {x: 52, y: 88.7, type: "B"},
            {x: 55, y: 93.3, type: "B"}, {x: 58, y: 97.9, type: "B"}, {x: 61, y: 102.5, type: "B"},
            {x: 64, y: 107.1, type: "B"}, {x: 67, y: 111.7, type: "B"}, {x: 70, y: 116.3, type: "B"},
            {x: 73, y: 120.9, type: "B"}, {x: 76, y: 125.5, type: "B"}, {x: 79, y: 130.1, type: "B"},
            {x: 82, y: 134.7, type: "B"}, {x: 85, y: 139.3, type: "B"}, {x: 88, y: 143.9, type: "B"},
            {x: 91, y: 148.5, type: "B"}, {x: 94, y: 153.1, type: "B"}, {x: 97, y: 157.7, type: "B"},
            // Type C: y = 3x + 0 + noise
            {x: 2, y: 6.1, type: "C"}, {x: 5, y: 15.2, type: "C"}, {x: 8, y: 24.6, type: "C"},
            {x: 11, y: 33.7, type: "C"}, {x: 14, y: 42.8, type: "C"}, {x: 17, y: 51.9, type: "C"},
            {x: 20, y: 60.5, type: "C"}, {x: 23, y: 69.4, type: "C"}, {x: 26, y: 78.5, type: "C"},
            {x: 29, y: 87.6, type: "C"}, {x: 32, y: 96.7, type: "C"}, {x: 35, y: 105.8, type: "C"},
            {x: 38, y: 114.9, type: "C"}, {x: 41, y: 124.0, type: "C"}, {x: 44, y: 133.1, type: "C"},
            {x: 47, y: 142.2, type: "C"}, {x: 50, y: 151.3, type: "C"}, {x: 53, y: 160.4, type: "C"},
            {x: 56, y: 169.5, type: "C"}, {x: 59, y: 178.6, type: "C"}, {x: 62, y: 187.7, type: "C"},
            {x: 65, y: 196.8, type: "C"}, {x: 68, y: 205.9, type: "C"}, {x: 71, y: 215.0, type: "C"},
            {x: 74, y: 224.1, type: "C"}, {x: 77, y: 233.2, type: "C"}, {x: 80, y: 242.3, type: "C"},
            {x: 83, y: 251.4, type: "C"}, {x: 86, y: 260.5, type: "C"}, {x: 89, y: 269.6, type: "C"},
            {x: 92, y: 278.7, type: "C"}, {x: 95, y: 287.8, type: "C"}, {x: 98, y: 296.9, type: "C"},
            {x: 100, y: 305.0, type: "C"}
        ];

    @Prop() myHight:number = 400
    @Prop() myWidth:number = 660

    @Prop() nameOfSactter:string ="Sample Scatter Plot"

    @Prop() colorOfPoint:string = "red"

    @Prop() xTitle:string = "X Axis"
    @Prop() yTitle:string = "Y Axis"

    @Prop() xTicks:number = 15
    @Prop() yTicks:number = 10

    private uniqueTypes: string[] = [];
    private colorScale!: d3.ScaleOrdinal<string, string>; // To store the difrent colors


    @Element() el!: HTMLElement; // Get a reference to the host element

    private resizeObserver!: ResizeObserver;//track size changes

    private prepareColorScale() {
        this.uniqueTypes = this.data.map(d => d.type).filter((v, i, a) => a.indexOf(v) === i);
        this.colorScale = d3.scaleOrdinal(d3.schemeCategory10).domain(this.uniqueTypes);// Initialize the color scale based on unique types
    }

    private observeResize() {
        this.resizeObserver = new ResizeObserver(entries => {//entries: array of the element on site(2li hwa div tag 2li 3ndo  Sactter plot)
        for (const entry of entries) {
            const { width, height } = entry.contentRect;//get the W and H for eacht current element
            if (Math.abs(this.myWidth - width) > 1 || Math.abs(this.myHight - height) > 1)
            {// lazmito hwa 2no mi3mlsh resize 3la kol mara 7ta lo hwa haga so8ira
            this.myWidth = width;
            this.myHight = height;
            this.drawChart();
            }
        }
        });
        const container = this.el.shadowRoot?.querySelector('.scatter-plot-ID');
        if (container) {
            this.resizeObserver.observe(container);//observe() dah al bi3ml resizing
        }
        else {
            console.warn('Scatter plot container (.scatter-plot-ID) not found in shadow DOM for resize observer.');
        }
    }

    drawChart()
    {
        const container = this.el.shadowRoot?.querySelector('.scatter-plot-ID');
        if (container) {
            container.innerHTML = '';
        }

        const isMobile = this.myWidth <500;
        const margin = {
            top: isMobile ? 50 : 70,
            right: 40,
            bottom: isMobile ? 50 : 60,
            left: isMobile ? 60 : 175
        };
        const width = this.myWidth - margin.left - margin.right;
        const height = this.myHight - margin.top - margin.bottom;

        const svg = d3.select(this.el.shadowRoot?.querySelector('.scatter-plot-ID')!)
            .append("svg")
            .attr("viewBox", `0 0 ${this.myWidth} ${this.myHight + (isMobile ? 60 : 0)}`)
            .attr("preserveAspectRatio", "xMidYMid meet")
            .append("g")
            .attr("transform", `translate(${margin.left},${margin.top})`);

        svg.append("text")
            .attr("class", "chart-title")
            .attr("x", width / 2)
            .attr("y", -20)
            .style("text-anchor", "middle")
            .style("font-size", isMobile ? '16px' : '20px')
            .text(this.nameOfSactter);

        // Scales and axes
        const xScale = d3.scaleLinear()
            .domain([0, d3.max(this.data, d => d.x)!])
            .range([0, width]);

        svg.append("g")
            .attr("class", "x-axis")
            .attr("transform", `translate(0, ${height})`)
            .call(d3.axisBottom(xScale).ticks(this.xTicks));

        const yScale = d3.scaleLinear()
            .domain([0, d3.max(this.data, d => d.y)!])
            .range([height, 0]);

        svg.append("g")
            .attr("class", "y-axis")
            .call(d3.axisLeft(yScale).ticks(this.yTicks));

        // Grid lines
        svg.append("g")
            .attr("class", "grid")
            .attr("transform", `translate(0, ${height})`)
            .call(d3.axisBottom(xScale).ticks(this.xTicks)
                .tickSize(-height).tickFormat(() => "")) // Remove tick labels
            .selectAll("line")

        svg.append("g")
            .attr("class", "grid")
            .call(d3.axisLeft(yScale).ticks(this.yTicks)
                .tickSize(-width).tickFormat(() => "")
            )

        // Data points
        svg.selectAll("circle")
            .data(this.data).enter()
            .append("circle")
            .attr("cx", d => xScale(d.x))
            .attr("cy", d => yScale(d.y))
            .attr("r", 5)
            .style("fill", d => this.colorScale(d.type) || this.colorOfPoint)
            .style("opacity", 0.8)
            .style("stroke", "black")
            .on("mouseover", (event, d: Datum) => {
                svg.selectAll<SVGCircleElement, Datum>("circle")
                .transition()
                .duration(200)
                .style("opacity", (p: any) => {
                    const currentDatum = p as Datum;
                    return (currentDatum.type === d.type || !currentDatum.type) ? 0.8 : 0.1;
                    });
                })
            .on("mouseout", () => {
                svg.selectAll("circle")
                .transition()
                .duration(500)
                .style("opacity", 0.8);
                });

        // Axis labels
        svg.append("text")
            .attr("class", "x-axis-label")
            .attr("x", width / 2)
            .attr("y", height + 40)
            .style("text-anchor", "middle")
            .text(this.xTitle);

        svg.append("text")
            .attr("class", "y-axis-label")
            .attr("x", -height / 2)
            .attr("y", -40)
            .attr("transform", "rotate(-90)")
            .style("text-anchor", "middle")
            .text(this.yTitle);

        // Legend
        const legendX = isMobile ? 10 : width + 10;
        if (this.uniqueTypes.length > 1) {
            const legend = svg.append("g")
                .attr("class", "legend")
                .attr("transform", `translate(${legendX}, 0)`);

            const legendSize = isMobile ? 12 : 18;

        this.uniqueTypes.forEach((type, i) => {
            legend.append('rect')
            .attr('x', 0)//relative to the legend group’s position (2sdi al legendX)
            .attr('y', i * (legendSize + 4))//stacked vertically, with each item shifted down and add a 4-pixel gap between items
            .attr('width', legendSize)
            .attr('height', legendSize)
            .attr('fill', this.colorScale(type));

            legend.append('text')
            .attr('x', legendSize + 5)
            .attr('y', i * (legendSize + 4) + legendSize / 1.5)
            .text(type)
            .style('font-size', isMobile ? '10px' : '12px');
            })
        }
    }

    @Watch('data')
    @Watch('myWidth')
    @Watch('myHight')
    @Watch('xTicks')
    @Watch('yTicks')
    @Watch('xTitle')
    @Watch('yTitle')
    @Watch('nameOfSactter')
    onPropChange() {
        this.prepareColorScale();
        this.drawChart();
    }

    componentWillLoad() {
        this.prepareColorScale()

    }
    componentDidLoad()
    {
        this.drawChart();
        this.observeResize();
    }

    disconnectedCallback() {
        if (this.resizeObserver) {
        this.resizeObserver.disconnect();
        }
    }
    render()
    {
        return(<div class="scatter-plot-ID"></div>)
    }
}
