:host {
  display: block;
  width: 100%;
  height: 400px;
  font-family: sans-serif;
}

.scatter-plot-ID {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Chart title */
.chart-title {
  font-weight: bold;
  font-size: 18px;
  fill: var(--theme-color-std-text);
}

/* Axis labels */
.x-axis-label,
.y-axis-label {
  font-size: 14px;
  fill: var(--theme-color-std-text);
}

/* Axis lines and ticks */
.x-axis path,
.y-axis path {
  stroke: var(--theme-chart-axes);
  stroke-width: 1.5;
}

.x-axis line,
.y-axis line {
  stroke: var( --theme-chart-ticks);
}

.x-axis text,
.y-axis text {
  font-size: 12px;
  fill:var(--theme-color-std-text);
}

/* Grid lines */
.grid line {
  stroke: var(--theme-chart-grid-lines);
  stroke-opacity: 0.5;
  shape-rendering: crispEdges;
}

/* Data points */
circle {
  cursor: pointer;
  transition: r 0.2s ease-out, fill 0.2s ease-in;
}

circle:hover {
  fill: orange;
  r: 6;
}

/* Legend */
.legend rect {
  opacity: 0.6;
}

.legend text {
  font-size: 12px;
  fill: var(--theme-color-std-text);
}
